import React from 'react'
import Similar from '../components/Similar'
import Helmet from '../components/Helmet'
const Privacy = () => {
    return (
        <>
        <Helmet title="Privacy">
        <Similar type = 'Popular'/>
        <div className='privacy'>
            <h1>Tuyên bố về quyền riêng tư</h1>
            <p>Tuyên bố về quyền riêng tư này giải thích các cách thức hoạt động của chúng tôi, 
                bao gồm cả các lựa chọn của bạn, liên quan đến việc thu thập, sử dụng và tiết lộ các thông tin nhất định, bao gồm cả thông tin cá nhân của bạn liên quan đến dịch vụ Da movie.</p>
            <h2>Liên hệ với chúng tôi</h2>
            <p>Nếu bạn có câu hỏi chung về tài khoản của mình hoặc về cách liên hệ với dịch vụ khách hàng để được hỗ trợ, vui lòng truy cập trung tâm trợ giúp trực tuyến của chúng tôi tại help.Da movie.com. Đối với các câu hỏi cụ thể về Tuyên bố về quyền riêng tư này, hoặc về việc chúng tôi sử dụng thông tin cá nhân của bạn, cookie hoặc các công nghệ tương tự, vui lòng liên hệ với Cán bộ Bảo vệ Dữ liệu/Văn phòng Phụ trách Quyền Riêng tư qua thư điện tử tại địa chỉ privacy@Da movie.com.</p>
            <p>Bên kiểm soát dữ liệu thông tin cá nhân của bạn là Da movie Pte. Ltd. Xin lưu ý rằng nếu bạn liên hệ với chúng tôi để yêu cầu hỗ trợ, chúng tôi có thể cần xác thực danh tính của bạn trước khi thực hiện yêu cầu của bạn để đảm bảo an toàn cho cả bạn và chúng tôi.</p>
            <h2>Thu thập thông tin</h2>
            <p>Thông tin bạn cung cấp cho chúng tôi: Chúng tôi thu thập thông tin mà bạn cung cấp cho chúng tôi, bao gồm:
tên, địa chỉ email, phương thức thanh toán, số điện thoại và các thông tin nhận dạng khác mà bạn có thể sử dụng (chẳng hạn như tên trong trò chơi). Chúng tôi thu thập thông tin này theo một số cách thức, bao gồm cả thông tin bạn nhập khi sử dụng dịch vụ của chúng tôi, tương tác với dịch vụ khách hàng của chúng tôi hoặc tham gia vào các cuộc khảo sát hoặc quảng cáo tiếp thị;
thông tin bạn chọn về xếp hạng, tùy chọn sở thích, cài đặt tài khoản (bao gồm cả các tùy chọn được thiết đặt trong phần "Tài khoản" trên trang web của chúng tôi), hoặc thông tin bạn cung cấp thông qua dịch vụ của chúng tôi hoặc dịch vụ của bên khác.</p>
            <p>Thông tin chúng tôi thu thập tự động: Chúng tôi thu thập thông tin về bạn và cách thức mà bạn sử dụng dịch vụ của chúng tôi, các tương tác của bạn với chúng tôi và quảng cáo của chúng tôi, cũng như thông tin về mạng, thiết bị mạng và máy tính hoặc các thiết bị khác tương thích với Da movie mà bạn có thể sử dụng để truy cập dịch vụ của chúng tôi (như hệ thống chơi game, TV thông minh, thiết bị di động, hộp giải mã tín hiệu truyền hình và các thiết bị truyền thông phát trực tuyến khác). Thông tin này bao gồm:
hoạt động của bạn trên dịch vụ Da movie, chẳng hạn như chọn tựa phim, chương trình bạn đã xem, cụm từ tìm kiếm và hoạt động trong trò chơi Da movie;
tương tác của bạn với thư điện tử, văn bản và tin nhắn của chúng tôi thông qua các kênh đẩy và nhắn tin trực tuyến;
chi tiết về các tương tác của bạn với dịch vụ khách hàng của chúng tôi, chẳng hạn như ngày, thời điểm và lý do liên hệ với chúng tôi, bản ghi chép lại của bất kỳ cuộc trò chuyện nào, cũng như số điện thoại của bạn và bản ghi âm cuộc gọi trong trường hợp bạn gọi cho chúng tôi;
mã nhận dạng thiết bị hoặc các thông tin nhận dạng duy nhất khác, bao gồm cả thông tin nhận dạng của các thiết bị mạng (chẳng hạn như bộ định tuyến) và các thiết bị tương thích với Da movie trên mạng của bạn;
các thông tin nhận dạng thiết bị có thể đặt lại (còn được gọi là thông tin nhận dạng quảng cáo), chẳng hạn như các thông tin nhận dạng trên thiết bị di động, máy tính bảng và thiết bị truyền thông phát trực tuyến chứa các thông tin nhận dạng đó (xem phần "Cookie và Quảng cáo trên mạng Internet" bên dưới để biết thêm thông tin);
các đặc điểm của thiết bị và phần mềm (như loại và cấu hình), thông tin kết nối bao gồm cả loại kết nối (Wi-Fi, mạng di động), số liệu thống kê về lượt xem trang, nguồn giới thiệu (ví dụ: URL giới thiệu), địa chỉ IP (mà có thể được sử dụng để cho chúng tôi biết về vị trí chung của bạn, chẳng hạn như thành phố, tiểu bang/tỉnh và mã bưu chính), thông tin đăng nhập máy chủ web tiêu chuẩn và trình duyệt;
thông tin được thu thập thông qua việc sử dụng cookie, tệp chỉ báo (web beacon) và các công nghệ khác, bao gồm cả thông tin quảng cáo (như thông tin về trạng thái sẵn có và việc cung cấp quảng cáo, URL của trang web, cũng như ngày giờ). (Xem phần "Cookie và Quảng cáo trên mạng Internet" của chúng tôi để biết thêm chi tiết.)</p>
        </div>
        </Helmet> 
        </>
    )
}

export default Privacy
